.slider {
  margin: 30px 0 0 0;
  height: 350px;
  // &__slide {
  //   width: 366px !important;
  //   transition: all .3s ease;
  // }
  &Item {
    position: relative;
    padding: 20px 20px 35px;
    border-radius: 21px;
    background-color: rgba(119, 44, 210, 0);
    border: solid 3px transparent;
    transition: all .3s ease;
    overflow: hidden;
    &.active {
      border: solid 3px rgba(255, 255, 255, 0.05);
      .sliderItem__glow {
        opacity: 1;
      }
    }
    &__glow {
      transition: all .3s ease;
      position: absolute;
      opacity: 0;
      left: 0;
      // z-index: 1;
      bottom: 0;
      width: 20px;
      height: 256px;
      // opacity: 0.3;
      border-radius: 175px;
      -webkit-filter: blur(50px);
      filter: blur(50px);
      background-color: #772cd2;
    }
    &__content {
      transition: all .3s ease;
      opacity: 0;
      visibility: hidden;
      // display: none;
      // position: absolute;
      // transform: scale(0);
      &.active {
        opacity: 1;
        visibility: visible;
        // display: block;
        // transform: scale(1);
        // position: relative;
      }
    }
    &__img {
      width: 320px;
      padding: 0 0 100% 0;
      background-color: rgba(68, 68, 68, 0.452);
      border-radius: 21px;
      overflow: hidden;
      position: relative;
      z-index: 1;
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
    }
    &__name {
      margin: 35px 0 0 0;
      font-family: AvenirNext;
      font-size: 22px;
      font-weight: bold;
      letter-spacing: -0.16px;
      color: #fff;
    }
    &__info {
      margin: 20px 0 0 0;
      display: flex;
      justify-content: space-between;
      &Item {
        font-family: AvenirNext;
        font-size: 10px;
        font-weight: 500;
        letter-spacing: -0.07px;
        color: rgba($color: #fff, $alpha: 0.5);
        span {
          display: block;
          margin: 10px 0 0 0;
          font-family: AvenirNext;
          font-size: 14px;
          font-weight: bold;
          letter-spacing: -0.1px;
          color: #fff;
        }
      }
    }
  }

  &__item {
    width: 366px !important;
    transition: all .3s ease;
  }


  // .swiper-slide {
